    min-height: 100vh;
    display: flex;
    flex-direction: column;
}
.results_page > * {
    padding: 0 5px;
    box-shadow: 0 3px 10px 4px rgba(0, 0, 0, 0.075);
}

.results_page > .app-header{
    padding: 4px 3%;
}

.results_page-decoration{
    display: none;
    width: 100%;
    height: 60px;
    -webkit-clip-path: polygon(100% 0, 95% 100%, 0 100%, 0 0);
            clip-path: polygon(100% 0, 95% 100%, 0 100%, 0 0);
    background-color: var(--color-primary-mid);
}

/* results_page-searchbox */
.results_page-searchbox{
    margin-top: 20px;
    position: relative;
    max-width: 800px;
    height: 50px;
    font-size: var(--size-normal);
    color:var(--font-mid);
}

/* the categories e.g comics, creators etc */
.results_page-categories{
    box-shadow: none;
    padding-top:20px;
    /*padding-bottom: 5px;*/
    display: flex;
    list-style-type: none;
    padding-left: 20px;
    border-bottom: 1px solid var(--font-lighter);
}
.results_page-category{
    color: var(--font-light);
}
.results_page-category > a{
    font-weight: var(--weight-mid);
    font-family: var(--roboto-bold);
    font-size: var(--size-mid);
    color: inherit;
    text-decoration: none;
}
.results_page-category:not(:last-child){
    margin-right: 15px;
}
.results_page-category-selected{
    color: var(--color-primary);
    border-bottom: 3px solid var(--color-primary);
}
.results_page-category-selected > a {
    font-weight: var(--weight-big);
}


/* results_page-content */
.results_page-content{
    box-shadow: none;
    padding: 0 15px;
    flex: 1 1;
    display: flex;
    flex-direction: column;
    background: var(--bg-main);
}
.results_page-content-searched-term{
    margin-top: 5px;
    padding: 15px 10px;
    background: white;
    color: var(--font-mid);
    font-size: var(--size-small);
    box-shadow: 0 3px 10px 2px rgba(0, 0, 0, 0.075);
}
.results_page-content-searched-term > p > span {
    margin: 5px;
    color: var(--color-primary);
    font-weight: var(--weight-big);
}

body{
    margin: 0;
}

.results_page-content-info{
    padding: var(--padding-vert-mobile) var(--padding-hori-mobile);
    background-color: white;
    margin-top: 20px;
    display: flex;
    justify-content: space-between;
}

.results_page-content-data{
    padding: 0 var(--padding-hori-mobile);
    margin-top: 20px;
    background-color: white;
    width: 100%;
    flex: 1 1;
    display: flex;
    flex-direction: column;
    list-style-type: none;
}

.results_page-content-data-item{
    height: 110px;
    display: flex;
    padding: 5px;
}
.results_page-content-data-item:not(:last-child){
    margin-bottom: 5px;
}
.results_page-content-data-item:first-child{
    margin-top: 5px;
    display: flex;
}

.results_page-content-data-item-thumbnail{
    flex: 0 0 74px;
    background-color: var(--bg-main);
    display: flex;
    align-items: center;
}
.results_page-content-data-item-thumbnail > img{
    height: 100%;
    width: 100%;
}

.results_page-content-data-item-text{
    padding: 5px 0;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    color: var(--font-mid);
}
.results_page-content-data-item-text > * {
    padding: 7px;
}
.results_page-content-data-item-text-title{
    font-size: var(--size-mid);
    font-weight: var(--weight-big);
    color: inherit;
    text-decoration: none;
}
.results_page-content-data-item-text:hover{
    color: var(--color-primary);
}

.results_page-content-data-item-price {
    color: var(--font-light);
}


.results_page-content-error{
    margin-top: 20px;
    flex: 1 1;
    background-color: white;
    display: flex;
    align-items: center;
    justify-content: center;
}

/* Breakpoints */
@media only screen and (min-width: 600px){
    .results_page-searchbox{
        margin-top: 0;
    }

    .results_page-decoration{
        display: block;
    }
    .results_page-content-data{
        margin-top: 20px;
        padding-top: 42px;
        display: grid;
        justify-content: center;
        grid-template-columns: repeat(auto-fill, 180px);
        grid-template-rows: auto;
        grid-column-gap: 10px;
    }
    .results_page-content-data-item{
        display: flex;
        flex-direction: column;
        height: auto;
        width: 180px;
    }
    .results_page-content-data-item-thumbnail{
        background-color: var(--bg-dark);
        min-height: 265px;
        height: auto;
        width: 100%;
    }
    .results_page-content-data-item:first-child{
        margin-top: 0;
    }
}
:root{
    --loader-bg:rgb(223, 223, 223);
}

.results_page-content-loader{
    margin-top: 20px;
    background-color: white;
    width: 100%;
    flex: 1 1;
    display: flex;
    flex-direction: column;
    list-style-type: none;
}

.results_page-content-loader-item{
    height: 110px;
    display: flex;
    padding: 5px;
}
.results_page-content-loader-item:not(:last-child){
    margin-bottom: 5px;
}
.results_page-content-loader-item:first-child{
    margin-top: 5px;
    display: flex;
}

.results_page-content-loader-item-thumbnail{
    flex: 0 0 87px;
    background-color: rgb(223, 223, 223);
    background-color: var(--loader-bg);
    position: relative;
    overflow-x: hidden;
}
.results_page-content-loader-item-thumbnail::after{
        width: 100%;
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        -webkit-transform: translateX(-100%);
                transform: translateX(-100%);
        background-image: linear-gradient(to right,rgb(223, 223, 223),rgb(238, 238, 238),rgb(223, 223, 223));
        background-image: linear-gradient(to right,var(--loader-bg),rgb(238, 238, 238),var(--loader-bg));
        -webkit-animation: shimmer 1.3s infinite;
                animation: shimmer 1.3s infinite;
        content: '';
}


.results_page-content-loader-item-text{
    width: 100%;
    padding: 5px 10px;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    color: var(--font-mid);
}
.results_page-content-loader-item-text > * {
    height: 28px;
    position: relative;
    overflow-x: hidden;
}
.results_page-content-loader-item-text > *::after{
    width: 100%;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    background-image: linear-gradient(to right,rgb(223, 223, 223),rgb(238, 238, 238),rgb(223, 223, 223));
    background-image: linear-gradient(to right,var(--loader-bg),rgb(238, 238, 238),var(--loader-bg));
    -webkit-animation: shimmer 1.3s infinite;
            animation: shimmer 1.3s infinite;
    content: '';
}


.results_page-content-loader-item-text-title{
    width: 100%;
    color: rgb(223, 223, 223);
    color: var(--loader-bg);
    background-color: currentColor;
    text-decoration: none;
}

.results_page-content-loader-item-price {
    background-color: rgb(223, 223, 223);
    background-color: var(--loader-bg);
    width: 80px;
}

